import React from 'react';
import './section.css';
import { cssFactory, siteCss } from '../common/css';

const css = cssFactory('section');

/* NOTE_1: Angabe nach 5 TMG ("Impressumspflicht") ntig (bei Umsatzsteuer (kein 'Kleinunternehmer') auch Umsatzsteuer-ID!)
 * https://www.bmjv.de/DE/Verbraucherportal/DigitalesTelekommunikation/Impressumspflicht/Impressumspflicht_node.html
 */
function Datenschutz() {
  return (
    <div className={siteCss('small')}>
      <div className={css('heading')}>
        Datenschutzerklrung
      </div>
      <div className={siteCss('par')}>
        Wenn Sie die Angebote der Internetseite deboradifonzo.de bzw. der freiberuflichen
  Lehrerin Chiara Debora Difonzo (im folgenden Kursanbieterin genannt) nutzen,
  werden personenbezogene Daten unter Beachtung der deutschen und der europischen
  Datenschutzgesetze verarbeitet.
  </div>
      <div className={siteCss('par')}>
        Die Erhebung, Speicherung und Verarbeitung personenbezogener Daten erfolgt gem
        dem Saarlndischen Gesetz zum Schutz personenbezogener Daten (Saarlndisches
        Datenschutzgesetz - SDSG), der europischen Datenschutz-Grundsatzverordnung
        (DSGVO) sowie dem Telemediengesetz (TMG). Die Kursanbieterin sowie ihre
        MitarbeiterInnen sind zur Verschwiegenheit und zur Einhaltung der
        Datenschutzbestimmungen verpflichtet worden.
  </div>
      <div className={siteCss('par')}>
        Die Kursanbieterin erhebt zur vertragliche Abwicklung von Sprachkursen (Anmeldung,
        Durchfhrung, Abrechnung), Einstufungen personenbezogenen Daten und verarbeitet
        diese.
  </div>
      <div className={siteCss('par')}>
        Die Kursanbieterin verarbeitet Ihre Daten gem dem Saarlndischen Gesetz zum Schutz
        personenbezogener Daten (Saarlndisches Datenschutzgesetz - SDSG): Es handelt sich
        um Verarbeitungsvorgnge, die fr die Erbringung der genannten Leistungen notwendig
        sind oder um Verarbeitungsvorgnge, die zur Durchfhrung vorvertraglicher
        Manahmen erforderlich sind.
  </div>
      <div className={siteCss('par')}>
        Aufgrund der gesetzlich vorgeschriebenen bzw. zugelassenen oder durch Ihre
        Einwilligung legitimierten Datenerhebung ist die Verarbeitung Ihrer personenbezogenen
        Daten rechtmig.
      </div>
    </div>
  );
}

export default Datenschutz;



